<template>


  <div>
    <h2 >{{ titulo }}</h2>
    <ul class = "list-group list-group-horizontal d-flex justify-content-center" >
      <li  v-for="opcion in opciones" :key="opcion.id"
          class= "list-group-item justify-center"
      >{{ opcion.titulo }}</li>
    </ul>



  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      msg: "mensaje",
      titulo: "CAC-Western Movies",
      opciones: [
        {id:1, titulo: "Tendencias" },
        {id:2, titulo: "Registrarse" },
        {id:3, titulo: "Iniciar Sesión" },
      ],

    }
  },
};
</script>



