<template>
  <div class="container">
    <hr>
    <form class="form-inline my-2">
      <input
        class="form-control mt-5"
        type="search"
        placeholder="Buscar película..."
        aria-label="Search"
      />
      <button class="btn btn-outline-success my-1 " type="submit">
        Search
      </button>
    </form>
    <hr>
  </div>
</template>

<script>
export default {
  name: "SearchBar",
  data() {
    return {
      msj: "Search bar",
    };
  },
};
</script>



