<template>

  <div class="container">
    <div>
      <SearchBar></SearchBar>
    </div>

    <h2 v-if="info == null">Cargando...</h2>

    <div class="d-flex flex-wrap justify-content-center">
      <div class="card mt-2" v-for="i in info" :key="i.id" style="width: 18rem;">
          <img :src="i.posterURL" alt="" class="card-img-top"/>
          <div class="card-body">
            <h5 class="card-title">{{ i.title }}</h5>
          </div>
      </div>
    </div>

      <!-- <ul class="list-group">
      <li v-for="i in info" :key="i.id" class="list-group-item">
        <h2>{{ i.title }}</h2>
        <img :src="i.posterURL" alt="" />
      </li>
    </ul> -->
  </div>
</template>

<script>
import SearchBar from './SearchBar.vue';

// import axios from 'axios';
export default {
  name: "Main",
  data() {
    return {
      info: null,
    };
  },
  methods: {
    fetchData() {
      fetch("https://api.sampleapis.com/movies/western")
        .then((response) => {
          response.json().then((data) => {
            console.log(data);
            this.info = data;
          });
        })
        .catch((err) => {
          this.info = `Error*: ${err}`;
        });
    },
  },
  mounted() {
    this.fetchData();
    // axios
    //   .get('https://api.coindesk.com/v1/bpi/currentprice.json')
    //   .then(response => (this.info = response))
    //   .catch(err => (this.info = "Error papu"))
  },
};
</script>



